'use client'

import { forwardRef, Ref } from "react"

export interface FormInputProps {
    type: string,
    placeholder: string,
    label: string,
    onChange?: (event: React.ChangeEvent<HTMLInputElement>) => void,
    value?: string,
    errorMessage?: string,
    ref?: Ref<HTMLInputElement>
}

export const FormInput = ({ label, errorMessage, type, placeholder, onChange, value, ref }: FormInputProps) => {
    return (
        <label className="flex flex-col gap-[2px] font-normal">
            <span className="font-light text-gray-400 text-sm pl-2">
                {label}
            </span>
            <input className="text-black-400 p-0.5 w-70 h-10 border-1 border-blue-300 rounded pl-2" type={type} placeholder={placeholder} value={value} onChange={onChange} ref={ref}/>
            <span>{errorMessage}</span>
        </label>
    )
}