'use client'

import { z } from "zod"
import { Button } from "../ui/Button/Button"
import { FormInput } from "../ui/FormInput/FormInput"
import { useForm } from "react-hook-form"
import { zodResolver } from "@hookform/resolvers/zod";

import Link from "next/link"
import { FormEvent, useEffect, useState } from "react"
import { api } from "@/pages/api/api"
import { Config } from "@/shared/types/config.types"
import { useSelector } from "react-redux"

const authFormSchema = z.object({
    email: z.string().nonempty("E-mail обязателен"),
    password: z.string().nonempty("Пароль обязателен"),
})

type AuthFormType = z.infer<typeof authFormSchema>

function AuthForm() {

    const [email, setEmail] = useState('')
    const [password, setPassword] = useState('')
    const [data, setData] = useState({})
    const [config, setConfig] = useState<Config | null>(null);
    

    const { register, handleSubmit, formState: { errors } } = useForm<AuthFormType>({
        resolver: zodResolver(authFormSchema)
    })

    const configure = useSelector((state) => state)


    useEffect(() => {
      async function fetchConfig() {
        // 1. Получаем конфиг с API Next.js
        const resConfig = await fetch('/api/config');
        const config = await resConfig.json();
        setConfig(config);
      }
      console.log(configure)
      fetchConfig();
    }, []);

    const onSubmit = (e: FormEvent<HTMLFormElement>, data: AuthFormType) => {
        e.preventDefault()

        if(!config) {
            return null
        }

        api('auth', data, config)

    }


    return (
        <>
            <form onSubmit={(e) => onSubmit(e, { email, password })} name="authForm" className="flex flex-col gap-[20px] items-center justify-center border-1 border-sky-300 pt-15 pb-15 pl-10 pr-10 rounded-[20px] shadow-md max-w-fit">
                <h2 className="font-medium text-lg m-0">Авторизация</h2>
                <div className="flex flex-col gap-[12px]">
                    <FormInput placeholder="E-mail" type="text" label="E-mail" value={email} onChange={(e) => setEmail(e.target.value)} />
                    <FormInput placeholder="Пароль" type="password" label="Пароль" value={password} onChange={(e) => setPassword(e.target.value)} />
                </div>
                <Button classname={"font-medium bg-blue-500 w-40 h-10 rounded flex justify-center items-center text-white cursor-pointer hover:bg-blue-700 hover:shadow-lg transition duration-300 ease-in-out"} type="submit">
                    Войти
                </Button>
                <Link href={'/register'}>
                    <Button classname="cursor-pointer">
                        Регистрация
                    </Button>
                </Link>
            </form>
        </>
    )
}

export default AuthForm